import React, { Component } from 'react';
import { Grid, Row, Col } from 'react-bootstrap';
import Header from './Header';
import Rankings from './Rankings';
import Recruitment from './Recruitment';
import './Boosting.css';

export default class Boosting extends Component {
  componentDidMount() {
    document.title = "Boosting - The Brigade";
  }
  render () {
    return (
      <div>
        <Header name="boosting-jumbotron" text="Brigade Boost Runs" flavor="Your wish is our command!" />
        <Grid>
          <Row>
            <Col xs={0} sm={0} md={3}>
              <Recruitment />
              <Rankings />
            </Col>
            <Col xs={12} sm={12} md={9}>
              <div className="boosting">
                <h2>Brigade Boost Runs</h2>
                <div className="boosting-body">
                  <h2>Why choose us?</h2>
                  <div>
                    We're one of the highest ranked 2 day raiding guilds in the world and as a guild we've been selling boosts since HFC, with hundreds of satisfied customers.
                    While many other guilds are offering boost runs these days, we understand that you are paying for a service and we will ensure a certain quality that not many others can.
                    And while we consider our prices to be very competitive there might still be others that sell it cheaper, but our aim is to provide you with a service that you feel that you can trust.
                  </div>

                  <hr />

                  <h2>Available Services and Pricing</h2>
                  <h2 className="boostheader header-antorus">Antorus, the Burning Throne <span className="epic">Mythic</span></h2>

                  <h2 className="boost-item">Full Mythic Antorus (11/11) - <span className="legendary">7m</span></h2>
                  <div className="boost-description">Includes all loot (including Titanforges) for your class, 1 tier token per boss, Cutting Edge Achievement and "... the Titanslayer" title.</div>

                  <h2 className="boost-item">Full Mythic Antorus (11/11 + mount) - <span className="legendary">10.5m</span></h2>
                  <div className="boost-description">Includes all loot (including Titanforges) for your class, 1 tier token per boss, Shackled Ur'zul Mount, Cutting Edge Achievement and "... the Titanslayer" title.</div>

                  <h2 className="boost-item">Mythic Argus - <span className="legendary">2.5m</span></h2>
                  <div className="boost-description">Includes all loot (including Titanforges) for your class, Cutting Edge Achievement and "... the Titanslayer" title.</div>

                  <h2 className="boost-item">Mythic Argus (+ mount) - <span className="legendary">7.5m</span></h2>
                  <div className="boost-description">Includes all loot (including Titanforges) for your class, Shackled Ur'zul Mount, Cutting Edge Achievement and "... the Titanslayer" title.</div>

                  <h2 className="boost-item">Mythic Garothi to Coven (9/11) - <span className="legendary">3.5m</span></h2>
                  <div className="boost-description">Includes all loot (including Titanforges) for your class, 1 tier token per boss, 9x Mythic Achievements.</div>

                  <h2 className="boost-item">Mythic Garothi to Aggramar (10/11) - <span className="legendary">4.5m</span></h2>
                  <div className="boost-description">Includes all loot (including Titanforges) for your class, 1 tier token per boss, 10x Mythic Achievements.</div>

                  <h2 className="boostheader header-nighthold">The Nighthold <span className="epic">Mythic</span></h2>
                  
                  <h2 className="boost-item">Mythic Gul’dan (+ mount) - <span className="legendary">1.2m</span></h2>
                  <div className="boost-description">Includes all loot (including Titanforges) for your class, Hellfire Infernal Mount, Elisande/Guldan Achievements and "..., Vengeance Incarnate" title.</div>

                  <h2 className="boostheader header-mythicplus">Dungeons / Mythic <span className="legendary">+</span></h2>
                  
                  <h2 className="boost-item">Mythic +15 Dungeon - <span className="legendary">140k</span></h2>
                  <div className="boost-description">Includes any leftover loot for your class, 960+ Item Level Weekly Chest Reward and Keystone Master Achievement.</div>

                  <hr />

                  <h2>Additional Information / FAQ</h2>
                  
                  <div className="boosting-question">Q: My character is very undergeared, can i still come?</div>
                  <div>A: Yes, there are no additional requirements for our boosts outside of the gold payment.</div>

                  <div className="boosting-question">Q: How is the payment handled?</div>
                  <div>
                    A: All runs require a 20% non refundable deposit upfront when your spot is reserved in order to make sure you turn up. 
                    The remaining 80% will be paid right before we start the boost.
                  </div>
                  <div className="boosting-important">
                    We only accept in game gold on The Maelstrom, it's connected realms and Kazzak. 
                    For other realms there will be an additional fee of 1 million gold for cross realms and 2 million gold for cross realm + cross faction transactions. 
                    This rule does not always apply to every scenario however, so if you're interested in paying from a different realm/faction it's best to contact us about it.
                  </div>
                  
                  <div className="boosting-question">Q: When do you do the boost runs?</div>
                  <div>A: All runs (except for Mythic+ and the occasional Gul'dan mount run) is done on <span className="boosting-bold">Fridays at 20 server time!</span></div>

                  <hr />

                  <h2>Contact</h2>
                  <div>If you want more information or are interested in purchasing any of our services, please contact: <span className="boosting-bold blue">Mawn#2810</span> or <span className="boosting-bold blue">Kai#21311</span> on Battle.net.</div>
                  <div>Thank you for showing interest in The Brigade Boosting and we look forward to hearing from you! :)</div>
                </div>
              </div>
            </Col>
          </Row>
        </Grid>
      </div>
    )
  }
}


