import React, { Component } from 'react';
import { Grid, Row, Col } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import './Footer.css';

export default class Footer extends Component {
  render () {
    return (
      <footer>
        <Grid>
          <div className="footer-section">
            <Row id="footer-row">
              <Col md={4} className="hidden-xs hidden-sm">
                <div id="image">
                  <img src="/assets/logo.png" alt="Logo" height="40%" width="40%"/>
                </div>
              </Col>
              <Col xs={6} md={4} className="col-bordered">
                <h5 className="hidden-xs hidden-sm">Site Navigation</h5>
                <h5 className="hidden-md hidden-lg">Navigation</h5>
                <ul>
                  <li className="footer-navigation"><Link to="/" className="x">Home</Link></li>
                  <li className="footer-navigation"><Link to="/about">About</Link></li>
                  <li className="footer-navigation"><Link to="/boosting">Boosting</Link></li>
                  <li className="footer-navigation"><Link to="/apply">Apply</Link></li>
                  <li className="footer-navigation"><Link to="/media">Media</Link></li>
                  <li className="footer-navigation-streams"><Link to="/streams">Streams</Link></li>
                </ul>
              </Col>
              <Col xs={6} md={4}>
                <h5>Links</h5>
                <ul>
                  <li className="footer-navigation-wowprogress"><a href="https://www.wowprogress.com/guild/eu/the-maelstrom/The+Brigade" target="_blank" rel="noopener noreferrer">WoWProgress</a></li>
                  <li className="footer-navigation-raiderio"><a href="https://raider.io/guilds/eu/the-maelstrom/The%20Brigade" target="_blank" rel="noopener noreferrer">Raider.IO</a></li>
                  <li className="footer-navigation-warcraftlogs"><a href="https://www.warcraftlogs.com/guilds/4923" target="_blank" rel="noopener noreferrer">WarcraftLogs</a></li>
                  <li className="footer-navigation-discord"><a href="https://discordapp.com/invite/rZ4JnRg" target="_blank" rel="noopener noreferrer">Discord</a></li>
                  <li className="footer-navigation-youtube"><a href="http://www.youtube.com/user/BrigadeGuild/" target="_blank" rel="noopener noreferrer">YouTube</a></li>
                  <li className="footer-navigation-contact"><a href="mailto:jesper.ohman.1@gmail.com">Report a bug</a></li>
                </ul>
              </Col>
            </Row>
            <div class="footer-copyright">
                <p>© {(new Date()).getFullYear()} The Brigade. All rights reserved. </p>
            </div>
          </div>
        </Grid>
      </footer>
    )
  }
}


