import React, { Component } from 'react';
import { Navbar, Nav, NavItem } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import './CustomNavbar.css';

export default class CustomNavbar extends Component {
  render () {
    return (
      <Navbar inverse collapseOnSelect fixedTop>
        <Navbar.Header>
          <Navbar.Brand>
            <img className="brand-icon" src="/favicon.png" alt="Reuben"/>
            THE BRIGADE
          </Navbar.Brand>
          <Navbar.Toggle />
        </Navbar.Header>
        <Navbar.Collapse>
          <Nav>
            <NavItem eventKey={1} componentClass={Link} active={window.location.pathname === '/'} href="/" to="/" className="left">Home</NavItem>
            <NavItem eventKey={2} componentClass={Link} active={window.location.pathname === '/about'} href="/about" to="/about" className="">About</NavItem>
            <NavItem eventKey={3} componentClass={Link} active={window.location.pathname === '/boosting'} href="/boosting" to="/boosting" className="">Boosting</NavItem>
            <NavItem eventKey={4} componentClass={Link} active={window.location.pathname === '/apply'} href="/apply" to="/apply" className="">Apply</NavItem>
          </Nav>
          <Nav pullRight>
            <NavItem eventKey={5} href="https://www.wowprogress.com/guild/eu/the-maelstrom/The+Brigade" target="_blank" className="wowprogress left">WoWProgress <img src="/assets/wowprog.png" alt="" width="20px" height="20px"/></NavItem>
            <NavItem eventKey={6} href="https://www.warcraftlogs.com/guilds/4923" target="_blank" className="warcraftlogs">WarcraftLogs <img src="/assets/wclogs.png" alt="" width="20px" height="20px"/></NavItem>
            <NavItem eventKey={7} href="https://discordapp.com/invite/rZ4JnRg" target="_blank" className="discord">Discord <i class="fab fa-discord fa-lg" aria-hidden="true"></i></NavItem>
            <NavItem eventKey={8} href="http://www.youtube.com/user/BrigadeGuild/" target="_blank" className="youtube">Youtube <i class="fab fa-youtube fa-lg" aria-hidden="true"></i></NavItem>
            <NavItem eventKey={9} componentClass={Link} active={window.location.pathname === '/streams'} href="/streams" to="/streams" className="twitch">Streams <i class="fab fa-twitch fa-lg" aria-hidden="true"></i></NavItem>
          </Nav>
        </Navbar.Collapse>
      </Navbar>
    )
  }
}


