import React, { Component } from 'react';
import { Grid, Row, Col } from 'react-bootstrap';
import Header from './Header';
import Rankings from './Rankings';
import Recruitment from './Recruitment';
import StreamBox from './StreamBox';
import './Streams.css';

export default class Streams extends Component {
  componentDidMount() {
    document.title = "Streams - The Brigade";
  }
  render () {
    return (
      <div>
        <Header name="streams-jumbotron" text="Brigade Streams" flavor="Time to get your PogChamps out!" />
        <Grid>
          <Row>
            <Col xs={0} sm={0} md={3}>
              <Recruitment />
              <Rankings />
            </Col>
            <Col xs={12} sm={12} md={9}>
              <div className="streams">
                <h2>Brigade Streams</h2>
                <div className="streams-body">
                  <Row>
                    <StreamBox text="Blur - Marksmanship" class="hunter" channel="blurish" />
                    <StreamBox text="Cerli - Frost" class="deathknight" channel="cerli" />
                    <StreamBox text="Conviq - Affliction" class="warlock" channel="conviq" />
                    <StreamBox text="Dex - Brewmaster" class="monk" channel="dexyn159" />
                    <StreamBox text="Dudeknight - Blood" class="deathknight" channel="dudeknighttv" />
                    <StreamBox text="Eowor - Holy" class="paladin" channel="eowor2" />
                    <StreamBox text="Ganx - Fury" class="warrior" channel="ganx1994" />
                    <StreamBox text="Higs - Restoration" class="shaman" channel="blindlad" />
                    <StreamBox text="Mawn - Retribution" class="paladin" channel="mawnan" />
                    <StreamBox text="Øffen - Shadow" class="priest" channel="offeneu" />
                    <StreamBox text="Partlycloudy - Balance" class="druid" channel="cloudberrylive" />
                    <StreamBox text="Powelex - Havoc" class="demonhunter" channel="powelex" />
                    <StreamBox text="Radikael - Windwalker" class="monk" channel="radicaL3i" />
                    <StreamBox text="Reuben - Restoration" class="druid" channel="aceof" />
                    <StreamBox text="Zupu - Marksmanship" class="hunter" channel="zuupu" />
                  </Row>
                </div>
              </div>
            </Col>
          </Row>
        </Grid>
      </div>
    )
  }
}


