import React, { Component } from 'react';
import './Rankings.css';

export default class Rankings extends Component {
  constructor() {
    super();
    this.state = {
      rankingsData: {}
    }
  }
  componentDidMount() {
    fetch('https://raider.io/api/v1/guilds/profile?region=eu&realm=The%20Maelstrom&name=The%20Brigade&fields=raid_rankings')
    .then(response => response.json())
    .then(data => this.setState({rankingsData: {name: data.name,
                                                rank_world: data.raid_rankings["antorus-the-burning-throne"].mythic["world"],
                                                rank_region: data.raid_rankings["antorus-the-burning-throne"].mythic["region"],
                                                rank_realm: data.raid_rankings["antorus-the-burning-throne"].mythic["realm"]
                                               }}))
  }
  render () {
    return (
      <div className="rankings hidden-xs hidden-sm">
        <h2>Progression</h2>
        <div className="rankings-body">
          <h3>The Brigade</h3>
          <div className="rankings-item">
            <span>World Rank: </span>
            <span className="rank epic">{this.state.rankingsData.rank_world}</span>
          </div>
          <div className="rankings-item">
            <span>Europe Rank: </span>
            <span className="rank epic">{this.state.rankingsData.rank_region}</span>
          </div>
          <div className="rankings-item">
            <span>Realm Rank: </span>
            <span className="rank legendary">{this.state.rankingsData.rank_realm}</span>
          </div>
        </div>
        <div className="rankings-footer">
        <a href="https://raider.io/guilds/eu/the-maelstrom/The%20Brigade" target="_blank" rel="noopener noreferrer" className="btn btn-block">View progress on Raider.IO</a>
        </div>
      </div>
    )
  }
}


