import React, { Component } from 'react';
import { Grid, Row, Col } from 'react-bootstrap';
import Header from './Header';
import MediaBox from './MediaBox';
import './Media.css';

export default class Media extends Component {
  componentDidMount() {
    document.title = "Media - The Brigade";
  }
  render () {
    return (
      <div>
        <Header name="media-jumbotron" text="Image Gallery" flavor="Where the memes glow brightest." />
        <Grid>
          <Row>
            <Col xs={12} sm={12} md={12}>
              <div className="media">
                <h2>All Images</h2>
                <div className="media-body">
                  <h2 className="tier-title">Tier 22 - Uldir, Halls of Control</h2>
                  <Row>
                  </Row>
                  <hr />

                  <h2 className="tier-title">Tier 21 - Antorus, the Burning Throne</h2>
                  <Row>
                    <MediaBox text="Argus the Unmaker" link="/brigade-app/assets/images/tier21/antorus-argus.jpg" />
                    <MediaBox text="Aggramar" link="/brigade-app/assets/images/tier21/antorus-aggramar.jpg" />
                    <MediaBox text="Coven of Shivarra" link="/brigade-app/assets/images/tier21/antorus-coven.jpg" />
                    <MediaBox text="Imonar, Kin'garoth and Varimathras" link="/brigade-app/assets/images/tier21/antorus-imonar-kingaroth-varimathras.jpg" />
                    <MediaBox text="Garothi, Dogs, Command, Keeper and Eonar" link="/brigade-app/assets/images/tier21/antorus-first-5.jpg" />
                  </Row>

                  <hr />

                  <h2 className="tier-title">Tier 20 - Tomb of Sargeras</h2>
                  <Row>
                    <MediaBox text="Kil'Jaeden" link="/brigade-app/assets/images/tier20/tomb-kiljaeden.jpg" />
                    <MediaBox text="Fallen Avatar" link="/brigade-app/assets/images/tier20/tomb-avatar.jpg" />
                    <MediaBox text="Maiden of Vigilance" link="/brigade-app/assets/images/tier20/tomb-maiden.jpg" />
                    <MediaBox text="Mistress Sassz'ine" link="/brigade-app/assets/images/tier20/tomb-mistress.jpg" />
                    <MediaBox text="The Desolate Host" link="/brigade-app/assets/images/tier20/tomb-host.jpg" />
                    <MediaBox text="Sisters of the Moon" link="/brigade-app/assets/images/tier20/tomb-sisters.jpg" />
                    <MediaBox text="Goroth, Demonic Inquisition and Harjatan" link="/brigade-app/assets/images/tier20/tomb-first-3.jpg" />
                  </Row>

                  <hr />

                  <h2 className="tier-title">Tier 19 - The Nighthold</h2>
                  <Row>
                   <MediaBox text="Gul'dan" link="/brigade-app/assets/images/tier19/nighthold-guldan.jpg" />
                   <MediaBox text="The Demon Within" link="/brigade-app/assets/images/tier19/nighthold-guldan-p3.jpg" />
                   <MediaBox text="Elisande" link="/brigade-app/assets/images/tier19/nighthold-elisande.jpg" />
                   <MediaBox text="Star Augur Etraeus" link="/brigade-app/assets/images/tier19/nighthold-augur.jpg" />
                   <MediaBox text="Tichondrius" link="/brigade-app/assets/images/tier19/nighthold-tichondrius.jpg" />
                   <MediaBox text="Spellblade Aluriel" link="/brigade-app/assets/images/tier19/nighthold-spellblade.jpg" />
                   <MediaBox text="High Botanist Tel'arn" link="/brigade-app/assets/images/tier19/nighthold-botanist.jpg" />
                   <MediaBox text="Krosus" link="/brigade-app/assets/images/tier19/nighthold-krosus.jpg" />
                   <MediaBox text="Trilliax" link="/brigade-app/assets/images/tier19/nighthold-trilliax.jpg" />
                   <MediaBox text="Skorpyron and Chronomatic Anomaly" link="/brigade-app/assets/images/tier19/nighthold-first-2.jpg" />
                  </Row>

                  <hr />

                  <h2 className="tier-title">Tier 19 - Trial of Valor</h2>
                  <Row>
                   <MediaBox text="Helya" link="/brigade-app/assets/images/tier19/trial-helya.jpg" />
                   <MediaBox text="Guarm" link="/brigade-app/assets/images/tier19/trial-guarm.jpg" />
                   <MediaBox text="Odyn" link="/brigade-app/assets/images/tier19/trial-odyn.jpg" />
                  </Row>

                  <hr />

                  <h2 className="tier-title">Tier 19 - The Emerald Nightmare</h2>
                  <Row>
                   <MediaBox text="Xavius" link="/brigade-app/assets/images/tier19/en-xavius.jpg" />
                   <MediaBox text="Cenarius" link="/brigade-app/assets/images/tier19/en-cenarius.jpg" />
                   <MediaBox text="Il'gynoth, Heart of Corruption" link="/brigade-app/assets/images/tier19/en-ilgynoth.jpg" />
                   <MediaBox text="Dragons of Nightmare" link="/brigade-app/assets/images/tier19/en-dragons.jpg" />
                   <MediaBox text="Ursoc" link="/brigade-app/assets/images/tier19/en-ursoc.jpg" />
                   <MediaBox text="Elerethe Renferal" link="/brigade-app/assets/images/tier19/en-elerethe.jpg" />
                   <MediaBox text="Nythendra" link="/brigade-app/assets/images/tier19/en-nythendra.jpg" />
                   <MediaBox text="Emerald Nightmare Heroic" link="/brigade-app/assets/images/tier19/en-heroic.jpg" />
                  </Row>

                  <hr />

                  <h2 className="tier-title">Tier 18 - Hellfire Citadel</h2>
                  <Row>
                   <MediaBox text="Archimonde" link="/brigade-app/assets/images/tier18/hfc-archimonde.jpg" />
                   <MediaBox text="Mannoroth" link="/brigade-app/assets/images/tier18/hfc-mannoroth.jpg" />
                   <MediaBox text="Xhul'horac" link="/brigade-app/assets/images/tier18/hfc-xhulhorac.jpg" />
                   <MediaBox text="Tyrant Velhari" link="/brigade-app/assets/images/tier18/hfc-velhari.jpg" />
                   <MediaBox text="Fel Lord Zakuun" link="/brigade-app/assets/images/tier18/hfc-zakuun.jpg" />
                   <MediaBox text="Shadow-Lord Iskar" link="/brigade-app/assets/images/tier18/hfc-iskar.jpg" />
                   <MediaBox text="Socrethar the Eternal" link="/brigade-app/assets/images/tier18/hfc-socrethar.jpg" />
                   <MediaBox text="Gorefiend" link="/brigade-app/assets/images/tier18/hfc-gorefiend.jpg" />
                   <MediaBox text="Kilrogg Deadeye" link="/brigade-app/assets/images/tier18/hfc-kilrogg.jpg" />
                   <MediaBox text="Kormrok" link="/brigade-app/assets/images/tier18/hfc-kormrok.jpg" />
                   <MediaBox text="Hellfire Assault" link="/brigade-app/assets/images/tier18/hfc-assault.jpg" />
                  </Row>

                  <hr />

                  <h2 className="tier-title">Tier 17 - Blackrock Foundry</h2>
                  <Row>
                   <MediaBox text="Blackhand" link="/brigade-app/assets/images/tier17/brf-blackhand.jpg" />
                   <MediaBox text="The Blast Furnace" link="/brigade-app/assets/images/tier17/brf-furnace.jpg" />
                   <MediaBox text="The Iron Maidens" link="/brigade-app/assets/images/tier17/brf-ironmaidens.jpg" />
                   <MediaBox text="Operator Thogar" link="/brigade-app/assets/images/tier17/brf-operator.jpg" />
                   <MediaBox text="Kromog" link="/brigade-app/assets/images/tier17/brf-kromog.jpg" />
                   <MediaBox text="Gruul" link="/brigade-app/assets/images/tier17/brf-gruul.jpg" />
                   <MediaBox text="Flamebender Ka'graz" link="/brigade-app/assets/images/tier17/brf-kagraz.jpg" />
                   <MediaBox text="Hans'gar and Franzok" link="/brigade-app/assets/images/tier17/brf-hansfranz.jpg" />
                   <MediaBox text="Oregorger" link="/brigade-app/assets/images/tier17/brf-oregorger.jpg" />
                   <MediaBox text="Beastlord Darmac" link="/brigade-app/assets/images/tier17/brf-darmac.jpg" />
                   <MediaBox text="Blackrock Foundry Heroic" link="/brigade-app/assets/images/tier17/brf-heroic.jpg" />
                  </Row>

                  <hr />

                  <h2 className="tier-title">Tier 17 - Highmaul</h2>
                  <Row>
                   <MediaBox text="Ko'ragh" link="/brigade-app/assets/images/tier17/hm-koragh.jpg" />
                   <MediaBox text="The Butcher" link="/brigade-app/assets/images/tier17/hm-butcher.jpg" />
                   <MediaBox text="Tectus" link="/brigade-app/assets/images/tier17/hm-tectus.jpg" />
                   <MediaBox text="Brackenspore" link="/brigade-app/assets/images/tier17/hm-brackenspore.jpg" />
                   <MediaBox text="Twin Ogron" link="/brigade-app/assets/images/tier17/hm-twins.jpg" />
                  </Row>
                </div>
              </div>
            </Col>
          </Row>
        </Grid>
      </div>
    )
  }
}


