import React, { Component } from 'react';
import { Grid, Row, Col, ResponsiveEmbed } from 'react-bootstrap';
import Header from './Header';
import Rankings from './Rankings';
import Recruitment from './Recruitment';
import './About.css';

export default class About extends Component {
  componentDidMount() {
    document.title = "About Us - The Brigade";
  }
  render () {
    return (
      <div>
        <Header name="about-jumbotron" text="About the Guild" flavor="Or a history lesson about yoghurt?" />
        <Grid>
          <Row>
            <Col xs={0} sm={0} md={3}>
              <Recruitment />
              <Rankings />
            </Col>
            <Col xs={12} sm={12} md={9}>
              <div className="about-us">
                <h2>About The Brigade</h2>
                <div className="about-body">
                  <h2>Who are we?</h2>
                  <p>
                    Founded as a weekend alt guild in 2007, we've since transformed the guild into a competitive and efficient two night per week guild for players who don't have the time or energy to raid a typical weekday schedule.
                    The guild is comprised of various nationalities, some of whom have raided together since TBC. 
                    Several of us have previously raided hardcore in top 50 guilds and our aim is to steadily improve our world ranking every tier while continuing to raid on a much smaller schedule. 
                    We all thoroughly enjoy raiding and making efficient progress, even though we've simmered down to 8 hours per week. 
                    For all of our raiders this is their main raid for the week, so naturally people get excited about killing bosses. 
                    The guild is relatively active outside of the raid nights with alt raiding and M+.
                  </p>

                  <hr />

                  <h2>Raiding Environment</h2>
                  <p>
                    This is not a guild where the raid leader is yelling at the top of his lungs about whatever miniscule mistake you might have made.
                    We strive towards having a fun non-toxic environment and we will continue to take action against those who start to act out of line. 
                    That said we do have our fair share of banter and horrible jokes and puns, so having the ability to take a joke is a requirement!
                  </p>
                  <p>
                    For an inside look at the inner workings of the guild, we present you with an insightful video on the history of yoghurt: 
                  </p>

                  <ResponsiveEmbed a16by9>
                    <iframe title="Who really made Yoghurt?" src="https://www.youtube.com/embed/bTnZoTQJLOo" allowFullScreen></iframe>
                  </ResponsiveEmbed>

                  <hr />

                  <h2>The Management Team</h2>
                  <div className="officer epic">Reuben</div>
                  <div className="officer-description">
                    Freshly made Guild Master during Nighthold progression, he leads the guild with an iron fist, but a soft heart. 
                    Born and bred on the island known as the You Kei, he truly is a typical specimen representing his people, enjoying things such as tea and crumpets or bad dental hygiene. 
                    No one really knows his physical appeareance, except that he is blonde and missing small parts of his ear. Leading theory is that he is actually Asian.
                  </div>
                  <div>
                    How to please: Don't be shit, mate! <br />
                    Where can he usually be found: On Reddit, researching the latest boss strategy.
                  </div>
                  <div className="officer blue">Zupu</div>
                  <div className="officer-description">
                    The official shot-caller during raids, he likes talking in the most monotonous way possible. 
                    With a calm personality and lack of emotions, he has been known to be mistaken for an android. 
                    Don't be fooled by all of this though - the dude can scream louder than a feminist getting offended by priviliged cis white males! 
                    For reference: Blackhand kill video.
                  </div>
                  <div>
                    How to please: Follow his instructions to the letter! <br />
                    Where can he usually be found: Discord, if there are at least 2 people in the channel.
                  </div>
                  <div className="officer blue">Eowor</div>
                  <div className="officer-description">
                    Known as the nice guy from the officer team, he is actually a devious african-american, always plotting the next prank on his unsuspecting victims. 
                    He has a very soothing voice and has never been heard saying bad words. 
                    He is always looking for doing unconventional things during raids, such as placing feasts on unreachable surfaces.
                  </div>
                  <div>
                    How to please: Be Naguura, love! <br />
                    Where can he usually be found: In the open world, hunting some nerdy achievement, or in LFR causing mischief.
                  </div>
                  <div className="officer blue">Mawn</div>
                  <div className="officer-description">
                    The newest addition to the officer team, he is the official mastermind behind the boss strategies utilized by the guild, such as delaying titanforging on Argus. 
                    Rumour has it he has deep connections to the Silvermoon mafia, specializing in spicy memes. 
                    Usually talks in monotonous tone, like the other Swedish officer.
                  </div>
                  <div>
                    How to please: Dodge the wakes, dipshit! <br />
                    Where can he usually be found: In his lab, working on the latest WeakAuras.
                  </div>
                  <div className="officer blue">Bro</div>
                  <div className="officer-description">
                    The Alpha Male of the guild, a very handsome, intelligent and social person (also the person who wrote this whole page, if it wasn't clear). 
                    A lot can be said about him, but we wouldn't wanna spoil the surprise for you!
                  </div>
                  <div>
                    How to please: Subscribe to and comment on his lifting videos on Youtube! <br />
                    Where can he usually be found: Bottom of the dps meters.
                  </div>

                  <hr />

                  <h2>What are we looking for?</h2>
                  <p>
                    We're looking for like minded individuals who want to excel in raiding without the tiresome raid schedule. <br />
                    As we're only raiding two nights per week we expect recruits:
                  </p>

                  <div className="about-requirement">★ To be fully prepared, tactic wise, for the progress bosses of that particular week.</div>
                  <div className="about-requirement">★ Put the Guild first, sometimes you will have to do the shit job on a fight and sometimes you might even have to sit.</div>
                  <div className="about-requirement">★ Punctually logging in for raid invites.</div>
                  <div className="about-requirement">★ Good attendance, we don't over recruit so reliability is important.</div>
                  <div className="about-requirement">★ Able to listen to instructions via Discord and communicate on voice whenever needed.</div>
                  <div className="about-requirement">★ Thick skin - we are loud as a guild, always have been, always will be!</div>
                  <div className="about-requirement">★ <span className="about-important">Relevant</span> previous raiding experience.</div>
                  <div className="about-requirement">★ To be able to play their character to a very high level. 
                                                     During progression we want to be constrained only by time, and not by the performance of our members.
                  </div>
                  <div className="about-requirement">★ We currently don't enforce having alts, but if split raiding remains viable in BfA we will run at least one.</div>
                  <div className="about-requirement">★ We won't consider applicants looking to raid in another guild, while raiding with us at the same time. Also, we don't accept package deals (2+ people applying together)!</div>
                  <div className="about-requirement">★ And most importantly - innate love for raiding and wiping!</div>
                  
                  <hr/>

                  <h2>Previous Progress</h2>
                  <div className="about-raidhighlight">★ Argus Mythic: World <span className="epic">87</span> / EU <span className="epic">61</span>.</div>
                  <div className="about-raidhighlight">★ Kil'jaeden Mythic: World <span className="blue">167</span> / EU <span className="epic">98</span>.</div>
                  <div className="about-raidhighlight">★ Gul'dan Mythic: World <span className="blue">284</span> / EU <span className="blue">173</span>.</div>
                  <div className="about-raidhighlight">★ Helya Mythic: World <span className="blue">315</span> / EU <span className="blue">177</span>.</div>
                  <div className="about-raidhighlight expfirst">★ Xavius Mythic: World <span className="blue">402</span> / EU <span className="blue">245</span>.</div>
                  <div className="about-raidhighlight">★ Archimonde Mythic: World <span className="blue">280</span> / EU <span className="blue">151</span>.</div>
                  <div className="about-raidhighlight">★ Blackhand Mythic: World <span className="blue">471</span> / EU <span className="blue">255</span>.</div>
                  <div className="about-raidhighlight">★ Garrosh Hellscream Heroic: World <span className="blue">1017</span> / EU <span className="blue">588</span>.</div>
                  
                  <hr />

                  <h2>Our Raids</h2>
                  <div className="about-raidtime">★ Friday: 19:45-24:00</div>
                  <div className="about-raidtime">★ Saturday: 19:45-24:00</div>
                  <div>Very infrequently raiding hours may be extended slightly if close to achieving a boss kill.</div>
                  <div className="about-raidtime">Required AddOns in the guild are Angry Assignments, RCLootCouncil, BigWigs and WeakAuras.</div>
                </div>
              </div>
            </Col>
          </Row>
        </Grid>
      </div>
    )
  }
}


