import React, { Component } from 'react';
import { Grid, Row, Col, Carousel } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import Header from './Header';
import Rankings from './Rankings';
import Recruitment from './Recruitment';
import './Index.css';

export default class Index extends Component {
  componentDidMount() {
    document.title = "Home - The Brigade";
  }
  render () {
    return (
      <div>
        <Header name="home-jumbotron" text="Brigade Website 3.0" flavor="How long will this one last? :thinking:" />
        <Grid>
          <Row>
            <Col xs={0} sm={0} md={3}>
              <Recruitment />
              <Rankings />
            </Col>
            <Col xs={12} sm={12} md={9}>
              <div className="killshots">
                <h2>Kill Shots</h2>
                <Link to="/media"><span className="medialink">Link to all images</span></Link>
                <div className="killshots-body">
                  <Carousel>
                    <Carousel.Item>
                      <img alt="Argus" src="https://i.imgur.com/NMnfRRP.jpg" />
                    </Carousel.Item>
                    <Carousel.Item>
                      <img alt="Aggramar" src="https://i.imgur.com/Rpl97y0.jpg" />
                    </Carousel.Item>
                    <Carousel.Item>
                      <img alt="Coven of Shivarra" src="https://i.imgur.com/m9sWj3t.jpg" />
                    </Carousel.Item>
                    <Carousel.Item>
                      <img alt="Imonar/Kin'garoth/Tichondrius" src="https://i.imgur.com/Xa2rKS8.jpg" />
                    </Carousel.Item>
                    <Carousel.Item>
                      <img alt="Garothi/Felhounds/High Command/Portal Keeper/Eonar" src="https://i.imgur.com/5vmrPCl.jpg" />
                    </Carousel.Item>
                  </Carousel>
                </div>
              </div>
            </Col>
          </Row>
        </Grid>
      </div>
    )
  }
}


