import React, { Component } from 'react';
import { Col } from 'react-bootstrap';

export default class StreamBox extends Component {
  render () {
    return (
      <Col xs={12} sm={6} md={4}>
        <div className="streams-title">
          <img className={this.props.class} src={"/brigade-app/assets/images/classicons/" + this.props.class +".jpg"} alt={this.props.class} />
          <h5 className={this.props.class}>{this.props.text}</h5>
        </div>
        <a href={"https://twitch.tv/" + this.props.channel}>
          <div className="streambox">
            <img src={"/brigade-app/assets/images/crests/" + this.props.class +".png"} alt={this.props.class + " crest"} />
            <div className="overlay" />
          </div>
        </a>
      </Col>
    )
  }
}


