import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './Recruitment.css';

export default class Recruitment extends Component {
  render () {
    return (
      <div className="recruitment hidden-xs hidden-sm">
        <h2>Recruitment</h2>
        <div className="recruitment-body">
          <div className="recruitment-item">
            <span className="class deathknight">Death Knight</span>
            <span className="priority prioMedium">Medium</span>
          </div>
          <div className="recruitment-item">
            <span className="class demonhunter">Demon Hunter</span>
            <span className="priority prioLow">Low</span>
          </div>
          <div className="recruitment-item">
            <span className="class druid">Druid</span>
            <span className="priority prioLow">Low</span>
          </div>
          <div className="recruitment-item">
            <span className="class hunter">Hunter</span>
            <span className="priority prioLow">Low</span>
          </div>
          <div className="recruitment-item">
            <span className="class mage">Mage</span>
            <span className="priority prioLow">Low</span>
          </div>
          <div className="recruitment-item">
            <span className="class monk">Monk</span>
            <span className="priority prioLow">Low</span>
          </div>
          <div className="recruitment-item">
            <span className="class paladin">Paladin</span>
            <span className="priority prioLow">Low</span>
          </div>
          <div className="recruitment-item">
            <span className="class priest">Priest</span>
            <span className="priority prioLow">Low</span>
          </div>
          <div className="recruitment-item">
            <span className="class rogue">Rogue</span>
            <span className="priority prioHigh">High</span>
          </div>
          <div className="recruitment-item">
            <span className="class shaman">Shaman</span>
            <span className="priority prioLow">Low</span>
          </div>
          <div className="recruitment-item">
            <span className="class warlock">Warlock</span>
            <span className="priority prioLow">Low</span>
          </div>
          <div className="recruitment-item">
            <span className="class warrior">Warrior</span>
            <span className="priority prioHigh">High</span>
          </div>
        </div>
        <div className="recruitment-footer">
          <Link to="/apply"><button className="btn btn-block">Apply</button></Link>
        </div>
      </div>
    )
  }
}


