import React, { Component } from 'react';
import { Grid, Row, Col } from 'react-bootstrap';
import Header from './Header';
import Rankings from './Rankings';
import Recruitment from './Recruitment';
import './Apply.css';

export default class Apply extends Component {
  componentDidMount() {
    document.title = "Apply - The Brigade";
  }
  render () {
    return (
      <div>
        <Header name="apply-jumbotron" text="Join The Brigade" flavor="So you wanna be part of the team huh?" />
        <Grid>
          <Row>
            <Col xs={0} sm={0} md={3}>
              <Recruitment />
              <Rankings />
            </Col>
            <Col xs={12} sm={12} md={9}>
              <div className="apply">
                <h2>Application Information</h2>
                <div className="apply-body">
                  <div className="apply-important">Even if recruitment for your class is closed, we're still interested in skilled and experienced players who can outperform our current raiders, class/spec is irrelevant!</div>

                  <hr />

                  <h2>What are we looking for?</h2>
                  <p>
                    We're looking for like minded individuals who want to excel in raiding without the tiresome raid schedule. <br />
                    As we're only raiding two nights per week we expect recruits:
                  </p>

                  <div className="apply-requirement">★ To be fully prepared, tactic wise, for the progress bosses of that particular week.</div>
                  <div className="apply-requirement">★ Put the Guild first, sometimes you will have to do the shit job on a fight and sometimes you might even have to sit.</div>
                  <div className="apply-requirement">★ Punctually logging in for raid invites.</div>
                  <div className="apply-requirement">★ Good attendance, we don't over recruit so reliability is important.</div>
                  <div className="apply-requirement">★ Able to listen to instructions via Discord and communicate on voice whenever needed.</div>
                  <div className="apply-requirement">★ Thick skin - we are loud as a guild, always have been, always will be!</div>
                  <div className="apply-requirement">★ <span className="about-important">Relevant</span> previous raiding experience.</div>
                  <div className="apply-requirement">★ To be able to play their character to a very high level. 
                                                     During progression we want to be constrained only by time, and not by the performance of our members.
                  </div>
                  <div className="apply-requirement">★ We currently don't enforce having alts, but if split raiding remains viable in BfA we will run at least one.</div>
                  <div className="apply-requirement">★ We won't consider applicants looking to raid in another guild, while raiding with us at the same time. Also, we don't accept package deals (2+ people applying together)!</div>
                  <div className="apply-requirement">★ And most importantly - innate love for raiding and wiping!</div>
                  
                  <hr/>

                  <h2>Application/Trialing Process</h2>
                  <p>
                    Applications are handled on an external Applications Discord server, follow the instructions in the PM from Mee6 Bot to make an application. 
                    Remember that your application is likely the first impression you'll give to the rest of the guild, so put some effort into it.
                    If you submit an application we expect you to check it daily and to be available to answer any questions we might have about the application.
                    We generally make final decisions on applications during the weekends after our raids, so don't expect a decision until then.
                  </p><br />
                  <p>
                    The Trialing period usually lasts for around 4 weeks, although even if you pass your trial you will still have to prove yourself during progression.
                    We never recruit for the bench, so having solid attendance and coming prepared to every encounter is expected of you. 
                    Every progression roster is made with the goal of bringing the best 20 raiders for the fight regardless of guild ranks and seniority.
                  </p>
                  <p>
                    This is something we truly believe in, and we've benched officers on bosses where other raiders outperform them before.
                    This also means that if your class is not optimal for a certain encounter, we do expect you to be fine with sitting out for the betterment of the guild.
                    We're looking for team players, and while it sucks sitting out once in a while we're all working towards a greater common goal here.
                  </p>

                  <hr />

                  <h2>Our Raids</h2>
                  <div className="apply-raidtime">★ Friday: 19:45-24:00</div>
                  <div className="apply-raidtime">★ Saturday: 19:45-24:00</div>
                  <div>Invites generally go out shortly before 19:45 Server Time, you are expected to be online before then.</div><br />
                  <div>
                    We're very honest about our raiding schedule, what you see is what you get. 
                    There are no additional days or farm being done on a third day, although very infrequently raiding hours may be extended slightly if we are close to achieving a boss kill.
                  </div>
                  <div className="apply-raidtime">Required AddOns in the guild are Angry Assignments, RCLootCouncil, BigWigs and WeakAuras.</div>

                  <hr />

                  <h2>Contact</h2>
                  <div>If you want more information or questions regarding recruitment, please contact our recruitment officer: <span className="apply-bold blue">Bro#22814</span> on Battle.net.</div>
                  <div>Thank you for showing interest in The Brigade and we look forward to hearing from you! :)</div>
                  <div className="text-center">
                    <a href="https://discordapp.com/invite/ZgNKSNa" target="_blank" rel="noopener noreferrer" className="joinbtn btn btn-lg">Join the Application Discord</a>
                  </div>
                </div>
              </div>
            </Col>
          </Row>
        </Grid>
      </div>
    )
  }
}


